"use client";
import React, { useEffect, useRef, useState } from "react";

export default function ProfileModal({
  user,
  onClose,
}: {
  user?: { nickname?: string; avatar?: string; profession?: string; loginDays?: number };
  onClose: () => void;
}) {
  const designWidth = 720; // Ë®≠Ë®àÁ®øÂØ¨Â∫¶Ôºàgame_05 ÁöÑÂü∫Ê∫ñÂØ¨Â∫¶Ôºâ
  const [designHeight, setDesignHeight] = useState(480); // ÊúÉÂú®‰∏ãÊñπÁî® image natural ratio Êõ¥Êñ∞
  const wrapperRef = useRef<HTMLDivElement | null>(null);
  const [scale, setScale] = useState(1);

  // ËÆÄÂèñÂúñÁâá natural ratioÔºåÂèñÂæó designHeight
  useEffect(() => {
    const img = new Image();
    img.src = "/game_05.png";
    img.onload = () => {
      if (img.naturalWidth && img.naturalHeight) {
        setDesignHeight(Math.round((designWidth * img.naturalHeight) / img.naturalWidth));
      }
    };
  }, []);

  useEffect(() => {
    const calc = () => {
      if (!wrapperRef.current) return;
      // Ê†πÊìöË¶ñÁ™óÂØ¨È´òÂêåÊôÇÈôêÂà∂ scaleÔºåÈÅøÂÖçÂè™‰ª•ÂØ¨Â∫¶Á∏ÆÊîæÈÄ†ÊàêË∂ÖÂá∫È´òÂ∫¶
      const maxW = window.innerWidth * 0.92;
      const maxH = window.innerHeight * 0.92;
      const s = Math.min(1, maxW / designWidth, maxH / designHeight);
      setScale(s);
    };
    calc();
    window.addEventListener("resize", calc);
    return () => window.removeEventListener("resize", calc);
  }, []);

  useEffect(() => {
    const onKey = (e: KeyboardEvent) => {
      if (e.key === "Escape") onClose();
    };
    document.addEventListener("keydown", onKey);
    return () => document.removeEventListener("keydown", onKey);
  }, [onClose]);

  return (
    <div
      onClick={onClose}
      style={{
        position: "fixed",
        inset: 0,
        background: "rgba(0,0,0,0.55)",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        zIndex: 60000,
        padding: 20,
      }}
    >
      {/* unscaled centering container */}
      <div
        onClick={(e) => e.stopPropagation()}
        ref={wrapperRef}
        style={{
          transformOrigin: "center center",
          // Áî® flex ÁΩÆ‰∏≠Á∏ÆÊîæÂæåÁöÑ inner ÂÆπÂô®
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        {/* inner ‰ª• designWidth ÁÇ∫Âü∫Ê∫ñÔºåÊï¥ÂÄã inner ÊúÉË¢´ scaleÔºåÂÖßÈÉ®ÁµïÂ∞çÂÆö‰ΩçÁöÑÊñáÂ≠óÊúÉË∑üËëóÁ∏ÆÊîæ */}
        <div
          style={{
            width: designWidth,
            borderRadius: 12,
            overflow: "hidden",
            boxShadow: "0 12px 40px rgba(0,0,0,0.6)",
            background: "#111",
            color: "white",
            position: "relative",
            transform: `scale(${scale})`,
            transformOrigin: "center center", // ‰ª•‰∏≠ÂøÉÁ∏ÆÊîæÔºåÈÅøÂÖç‰ΩçÁßª
            margin: "0 auto",                 // Á¢∫‰øùÁΩÆ‰∏≠
          }}
        >
          {/* ËÉåÊôØÂúñ */}
          <img
            src="/game_05.png"
            alt="‰∫∫Áâ©Ë≥áË®ä"
            style={{ width: "100%", display: "block", objectFit: "cover" }}
          />

          {/* ‰ª•‰∏ãÊñáÂ≠ó/È†≠ÂÉèË¶ÜËìãÂçÄÂùáÁî®Ë®≠Ë®àÁ®øÂ∫ßÊ®ôÔºàÂú®ÂéüÊú¨Ê™îÊ°àÁöÑÁõ∏Âêå‰ΩçÁΩÆÔºâ */}
          {/* È†≠ÂÉèÔºàÁ¥ÖËâ≤Ê°ÜÂçÄÂüüÔºâ */}
          <div
            style={{
              position: "absolute",
              top: 88, // pxÔºàÂü∫Êñº designWidthÔºâ
              left: 290,
              width: 50,
              height: 50,
              borderRadius: 8,
              overflow: "hidden",
              background: "#333",
            }}
          >
            <img
              src="/game_06.png"
              alt="avatar"
              style={{ width: "100%", height: "100%", objectFit: "cover" }}
            />
          </div>

          {/* ËÅ∑Ê•≠ + ÁôªÂÖ•Â§©Êï∏ÔºàÂêå‰∏ÄË°åÔºåËÅ∑Ê•≠ÂæåÈù¢È°ØÁ§∫ÁôªÂÖ•Â§©Êï∏Ôºå‰∏≠ÈñìÊúâÈñìÈöîÁ¨¶ËôüÔºâ */}
          <div
            style={{
              position: "absolute",
              top: 105, // px
              left: 350,
              display: "flex",
              alignItems: "center",
              gap: 6,
              fontSize: 12,
              fontWeight: 700,
              color: "#fff",
              whiteSpace: "nowrap",
            }}
          >
            <span>{user?.profession || "ËÅ∑Ê•≠Êú™Ë®≠ÂÆö"}</span>
            {typeof user?.loginDays === "number" && (
              <span style={{ color: "rgba(255,255,255,0.95)", fontWeight: 600 }}>
                {"\u00A0„Éª\u00A0"}ÁôªÂÖ• {user!.loginDays} Â§©
              </span>
            )}
          </div>

          {/* Êö±Á®±ÔºàÂíñÂï°Ëâ≤Ê°ÜÂçÄÂüüÔºâ */}
          <div
            style={{
              position: "absolute",
              bottom: 88,
              left: 55,
              width: 200,
              textAlign: "center",
              fontSize: 18,
              fontWeight: "bold",
              color: "white",
            }}
          >
            {user?.nickname || "Ë®™ÂÆ¢"}
          </div>

          {/* ÈóúÈñâÊåâÈàïÔºàË®≠Âú® inner ÁöÑÂè≥‰∏ãÔºå‰∏çÊúÉË¢´ scale ÂΩ±Èüø‰∫íÂãïÔºâ */}
          <div style={{ position: "absolute", right: 20, bottom: 20 }}>
            <button
              onClick={onClose}
              style={{
                background: "rgba(0,0,0,0.6)",
                color: "white",
                border: "1px solid rgba(255,255,255,0.06)",
                padding: "8px 12px",
                borderRadius: 8,
                cursor: "pointer",
              }}
            >
              ÈóúÈñâ
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

