"use client";
import React, { useState, useRef, useEffect } from "react";
import ReactDOM from "react-dom";
import ProfileModal from "./ProfileModal";


type Message = {
  text: string;
  sender: "me" | "other";
  nickname?: string;
  avatar?: string; // ÂèØ‰ª•ÊòØÂúñÁâáÊàñÊï∏Â≠óÂ≠ó‰∏≤
};

let guestCounter = 1; // ÂÖ®ÂüüË∑Ø‰∫∫Ë®àÊï∏Âô®

export default function ChatBox({
  currentUser,
  portalScale = 1,
  usePortal = true,
  left = "24px",
  bottom = "40px",
  top,
  right,
  width = 200,
  height = 200,
  bubbleMinHeight = 36,        // ‚Üê ÊîπÂ∞èÊúÄ‰ΩéÈ´òÂ∫¶
  bubbleVerticalPadding = 6,   // ‚Üê ÊîπÂ∞è‰∏ä‰∏ãÂÖßË∑ù
}: {
  currentUser?: { nickname: string; avatar?: string; loggedIn?: boolean };
  portalScale?: number;
  usePortal?: boolean;
  left?: string | number;
  bottom?: string | number;
  top?: string | number;
  right?: string | number;
  width?: number | string;
  height?: number | string;
  bubbleMinHeight?: number;
  bubbleVerticalPadding?: number;
}) {
  const [messages, setMessages] = useState<Message[]>([]);
  const [input, setInput] = useState("");
  const messagesEndRef = useRef<HTMLDivElement | null>(null);
  const [mounted, setMounted] = useState(false);

  // Ê≠£Á¢∫ÔºöÊääÈÄôÂÖ©ÂÄã useState ÁßªÂà∞ÁµÑ‰ª∂ÂÖß
  const [showProfile, setShowProfile] = useState(false);
  const [selectedUser, setSelectedUser] = useState<{ nickname?: string; profession?: string; loginDays?: number } | null>(null);

  // Êñ∞Â¢ûÔºöÂú® client mount ÂæåÊ®ôË®òÂ∑≤ mountÔºåÂÖÅË®± portal Ê∏≤Êüì
  useEffect(() => {
    setMounted(true);
  }, []);

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const handleSend = () => {
    if (input.trim() === "") return;

    let nickname = "Ë∑Ø‰∫∫";
    let avatar = "";

    if (currentUser?.loggedIn) {
      nickname = currentUser.nickname || "Ë®™ÂÆ¢";
      avatar = currentUser.avatar || "üòÄ";
    } else {
      nickname = `Ë∑Ø‰∫∫${guestCounter}`;
      avatar = `${guestCounter}`; // Êï∏Â≠óÁõ¥Êé•Áï∂È†≠ÂÉè
      guestCounter++;
    }

    setMessages((prev) => [
      ...prev,
      { text: input.trim(), sender: "me", nickname, avatar },
    ]);
    setInput("");
  };

  const baseStyle: React.CSSProperties = {
    zIndex: 30000,
    background: "rgba(0,0,0,0.6)",
    borderRadius: 5,
    border: "1px solid rgba(255,255,255,0.08)",
    padding: 8,                 // Ê∏õÂ∞ëÊï¥È´îÂÖßË∑ù
    color: "white",
    fontSize: 11,               // Êï¥È´îÂ≠óÁ¥öÁï•Â∞è
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-between",
    boxShadow: "0 6px 18px rgba(0,0,0,0.4)",
    backdropFilter: "blur(4px)",
    boxSizing: "border-box",
  };

  const portalStyle: React.CSSProperties = {
    position: "fixed",
    left: left as any,
    bottom: bottom as any,
    top: top as any,
    right: right as any,
    transform: `scale(${portalScale})`,
    transformOrigin: "left bottom",
    willChange: "transform",
    width,
    height,
    ...baseStyle,
  };

  const inlineStyle: React.CSSProperties = {
    position: "absolute",
    left: left as any,
    bottom: bottom as any,
    top: top as any,
    right: right as any,
    transform: "none",
    width,
    height,
    ...baseStyle,
  };

  const jsx = (
    <div className="chatbox-portal" style={usePortal ? portalStyle : inlineStyle}>
      {/* Ê®ôÈ°åÂàó */}
      <div style={{ fontSize: 9, fontWeight: 700, marginBottom: 2 }}>
        Ë®™ÂÆ¢ËÅäÂ§©ÂÆ§
      </div>

      {/* Ë®äÊÅØÂàóË°® */}
      <div
        style={{
          flex: 1,
          overflowY: "auto",
          marginBottom: 5,
          display: "flex",
          flexDirection: "column",
          gap: 8,          
        }}
      >
        {messages.map((msg, idx) => (
          <div
            key={idx}
            style={{
              display: "flex",
              justifyContent: msg.sender === "me" ? "flex-end" : "flex-start",
              gap: 4, // ‚Üê Á∏ÆÂ∞èÂ∑¶Âè≥ÈñìË∑ùÔºàÊ≥°Ê≥°ËàáÈ†≠ÂÉèË∑ùÈõ¢Ôºâ
              alignItems: "center", // ÊîπÁÇ∫ÁΩÆ‰∏≠ÔºåËÆìÊ≥°Ê≥°Ëàá avatar ‰∏≠ÂøÉÂ∞çÈΩä
            }}
          >
            {/* È†≠ÂÉèÊ¨ÑÔºàÊö±Á®±‰ª•ÁµïÂ∞çÂÆö‰ΩçÈ°ØÁ§∫Ôºå‰∏çÂΩ±ÈüøÂàóÈ´òÂ∫¶Ôºâ */}
            <div
              style={{
                width: 20, // ‚Üê Á∏ÆÂ∞èÈ†≠ÂÉèÊ¨ÑÂØ¨Â∫¶ÔºåËÆìÊ≥°Ê≥°Êõ¥Èù†Ëøë
                boxSizing: "border-box",
                position: "relative", // ‰æõÊö±Á®±ÁµïÂ∞çÂÆö‰Ωç
                flexShrink: 0,
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <div
                style={{
                  width: 18,
                  height: 18,
                  borderRadius: "50%",
                  overflow: "hidden",
                  background: "gray",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  fontWeight: "bold",
                  cursor: "pointer", // ÂèØÈªûÊìäÊâìÈñã‰∫∫Áâ©Ë≥áË®ä
                }}
               onClick={() => {
                 setSelectedUser({ nickname: msg.nickname, avatar: msg.avatar });
                 setShowProfile(true);
               }}
              >
                {isNaN(Number(msg.avatar)) ? (
                  msg.avatar ? (
                    <img src={msg.avatar} alt="avatar" style={{ width: "100%", height: "100%", objectFit: "cover" }} />
                  ) : null
                ) : (
                  msg.avatar
                )}
              </div>

              {/* Êö±Á®±ÔºöÁµïÂ∞çÂÆö‰ΩçÂú®È†≠ÂÉè‰∏ãÊñπÔºå‰∏çÊúÉÂΩ±ÈüøÊ≥°Ê≥°Â∞çÈΩä */}
              <div
                style={{
                  position: "absolute",
                  left: "50%",
                  transform: "translateX(-50%)",
                  top: "calc(100% + 2px)", // ‚Üê ÈñìË∑ùÁî± 6px ÊîπÊàê 4pxÔºåÊîπÊõ¥Â∞èÂ∞±Áî® 2px Êàñ 0px
                  fontSize: 8,
                  lineHeight: 1,
                  color: "rgba(255,255,255,0.9)",
                  whiteSpace: "nowrap",
                  textAlign: "center",
                  pointerEvents: "none",
                }}
              >
                {msg.nickname}
              </div>
            </div>

            {/* Ê≥°Ê≥°ÔºöÂûÇÁõ¥ÁΩÆ‰∏≠ÔºàÊúÉËàá avatar ‰∏≠ÂøÉÂ∞çÈΩäÔºâ */}
            <div
              style={{
                background: msg.sender === "me" ? "rgba(206, 138, 106, 0.85)" : "rgba(255,255,255,0.08)",
                padding: "6px 10px",
                borderRadius: 6,
                maxWidth: "70%",
                wordBreak: "break-word",
                display: "flex",
                alignItems: "center",
              }}
            >
              <div style={{ fontSize: 9, lineHeight: 1.0 }}>{msg.text}</div>
            </div>
          </div>
        ))}
        <div ref={messagesEndRef} />
      </div>

      {/* Ëº∏ÂÖ•Ê°Ü */}
      <div style={{ display: "flex", gap: 6 }}>
        <input
          type="text"
          placeholder="Ëº∏ÂÖ•Ë®äÊÅØ..."
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyDown={(e) => {
            if (e.key === "Enter") {
              e.preventDefault();
              handleSend();
            }
          }}
          style={{
            flex: 1,
            fontSize: 9,
            border: "none",
            outline: "none",
            padding: 8,
            borderRadius: 6,
            background: "rgba(255,255,255,0.06)",
            color: "white",
          }}
        />
        <button
          onClick={handleSend}
          style={{
            padding: "8px 10px",
            fontSize: 9,
            cursor: "pointer",
            border: "none",
            borderRadius: 6,
            backgroundColor: "#6c4b2a",
            color: "white",
          }}
        >
          ÈÄÅÂá∫
        </button>
      </div>
    </div>
  );

  if (!mounted || typeof document === "undefined") return null;

  // ‰ª• portal Ê∏≤Êüì modalÔºàÈÅøÂÖçË¢´ ancestor transform ÂΩ±ÈüøÔºâ
  return (
    <>
      {usePortal ? ReactDOM.createPortal(jsx, document.body) : jsx}
      {showProfile && selectedUser &&
        ReactDOM.createPortal(
          <ProfileModal user={selectedUser} onClose={() => setShowProfile(false)} />,
          document.body
        )
      }
    </>
  );
}



